.sho-admin-overlay {
    position: fixed !important;
    z-index: 10;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,.4);
    pointer-events: visible; }

.sho-admin-modal {
    width: 70% !important;
    height: 80vh !important;
    position: fixed !important;
    @include center;
    z-index: 20;
    background: $color-modal;
    overflow-y: scroll;
    color: $color-font;
    padding: 2em !important;
    box-shadow: $shadow;
    font-size: 1.3em !important; }



